.tu-transfer {
  font-size: 14px; }
  .tu-transfer__buttons {
    display: inline-block;
    vertical-align: middle;
    padding: 0 10px; }
  .tu-transfer__button {
    display: block;
    margin: 0 auto;
    color: #9baacf; }
    .tu-transfer__button.is-disabled {
      color: #bec8e4; }
      .tu-transfer__button.is-disabled:hover {
        color: #bec8e4; }
    .tu-transfer__button:first-child {
      margin-bottom: 10px; }
    .tu-transfer__button:nth-child(2) {
      margin: 0; }
    .tu-transfer__button i,
    .tu-transfer__button span {
      font-size: 14px; }
    .tu-transfer__button [class*="tu-icon-"] + span {
      margin-left: 0; }

.tu-transfer-panel {
  border-radius: 4px;
  overflow: hidden;
  display: inline-block;
  vertical-align: middle;
  width: 200px;
  max-height: 100%;
  box-sizing: border-box;
  position: relative;
  box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff;
  transition: 0.3s ease; }
  .tu-transfer-panel__wrapper {
    height: 238px; }
  .tu-transfer-panel__body.is-with-footer {
    padding-bottom: 40px; }
  .tu-transfer-panel__list {
    margin: 0;
    padding: 6px 0;
    list-style: none;
    box-sizing: border-box; }
    .tu-transfer-panel__list.is-filterable {
      height: 190px;
      padding-top: 0; }
  .tu-transfer-panel__item {
    height: 28px;
    line-height: 28px;
    padding-left: 15px;
    display: block !important;
    transition: all 0.3s; }
    .tu-transfer-panel__item + .tu-transfer-panel__item {
      margin-left: 0; }
    .tu-transfer-panel__item:hover {
      color: #6d5dfc; }
  .tu-transfer-panel__filter {
    text-align: center;
    margin: 15px;
    box-sizing: border-box;
    display: block;
    width: auto; }
    .tu-transfer-panel__filter .tu-input__inner {
      width: 100%;
      font-size: 12px;
      display: inline-block;
      box-sizing: border-box;
      padding-right: 10px;
      padding-left: 30px; }
    .tu-transfer-panel__filter .tu-icon-circle-close {
      cursor: pointer; }
  .tu-transfer-panel .tu-transfer-panel__header {
    height: 40px;
    line-height: 40px;
    margin: 0;
    padding-left: 15px;
    border-bottom: 1px solid rgba(190, 200, 228, 0.38);
    box-sizing: border-box;
    color: #000000; }
    .tu-transfer-panel .tu-transfer-panel__header .tu-checkbox .tu-checkbox__label span {
      position: absolute;
      right: 15px;
      color: #bec8e4;
      font-size: 12px;
      font-weight: normal; }
  .tu-transfer-panel .tu-transfer-panel__footer {
    height: 40px;
    margin: 0;
    padding: 0;
    border-top: 1px solid rgba(190, 200, 228, 0.38);
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 1; }
    .tu-transfer-panel .tu-transfer-panel__footer::after {
      display: inline-block;
      content: "";
      height: 100%;
      vertical-align: middle; }
  .tu-transfer-panel .tu-transfer-panel__empty {
    margin: 0;
    height: 28px;
    line-height: 28px;
    padding: 6px 15px 0;
    color: #bec8e4;
    text-align: center; }
